const BASE_URL = 'http://localhost:5000'

/**
 * Utility to build full API URL
 */
const buildUrl = (path: string) => `${BASE_URL}${path}`

/**
 * Handles HTTP response errors with consistent formatting
 */
const handleResponse = async (res: Response) => {
  if (!res.ok) {
    const error = await res.json().catch(() => ({}))
    throw new Error(error.error || 'An unexpected error occurred')
  }
  return res.json()
}

/**
 * Generic GET request with session cookies
 */
export async function apiGet<T = any>(path: string): Promise<T> {
  const res = await fetch(buildUrl(path), {
    method: 'GET',
    credentials: 'include',
  })
  return handleResponse(res)
}

/**
 * Generic POST request with JSON body
 */
export async function apiPost<T = any>(
  path: string,
  data: Record<string, any>
): Promise<T> {
  const res = await fetch(buildUrl(path), {
    method: 'POST',
    credentials: 'include',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data),
  })
  return handleResponse(res)
}

/**
 * Submit multipart/form-data application form
 */
export async function submitApplication(
  form: Record<string, any>,
  requiredDocs: Record<string, File | null>,
  optionalDocs: { title: string; file: File | null }[]
): Promise<any> {
  const formData = new FormData()

  Object.entries(form).forEach(([key, value]) => {
    formData.append(key, value)
  })

  Object.entries(requiredDocs).forEach(([title, file]) => {
    if (file) {
      formData.append('documents', file, title)
    }
  })

  optionalDocs.forEach(({ title, file }) => {
    if (file && title) {
      formData.append('documents', file, `optional:${title}`)
    }
  })

  const res = await fetch(buildUrl('/api/client/apply'), {
    method: 'POST',
    credentials: 'include',
    body: formData,
  })

  return handleResponse(res)
}

/**
 * Auth: Fetch logged-in user details
 */
export async function fetchCurrentUser(): Promise<any> {
  return apiGet('/api/auth/me')
}

/**
 * Auth: Login user
 */
export async function loginUser(data: {
  pen_number: string
  password: string
}): Promise<any> {
  return apiPost('/api/auth/login', data)
}

/**
 * Auth: Register new user
 */
export async function registerUser(data: {
  first_name: string
  surname: string
  other_names?: string
  email: string
  pen_number: string
  password: string
}): Promise<any> {
  return apiPost('/api/auth/register', data)
}
